import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import UserIcon from "@material-ui/icons/AccountCircle";

import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { toast } from 'react-toastify';
import { Services } from "../../assets/services/Services.js";
import Cookies from 'js-cookie';
// import Loader from "react-loader-spinner";
import BarWave from "react-cssfx-loading/lib/Hypnosis";
function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright © '}
      <Link color="inherit" href="https://davidmaene.reitecinfo.net">
        Africa-Dev
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const theme = createTheme();

export default function SignIn() {
    const [isloading, setisloading] = React.useState(false);
    const [output, setoutput] = React.useState(null);
  
    function validateEmail(email) {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        // console.log(re.test(String(email).toLowerCase()));
        return re.test(String(email).toLowerCase());
    }
    const handleSubmit = (event) => {
        event.preventDefault();
        setoutput(null);
        const data = new FormData(event.currentTarget);
        if(validateEmail(data.get("email")) && data.get("password").length > 2){
            setisloading(true)
            Services._onLogin(data, (rejected, resolved) => {
                if(resolved){
                    setisloading(false);
                    try {
                        const status = parseInt(resolved['status']);
                        switch (status) {
                            case 200:
                                document.cookie = `sco-afd=${resolved['data']['token']}`;
                                localStorage.setItem("current-user", JSON.stringify(resolved['data']['user']));

                                // console.log(resolved);
                                setoutput(null);
                                toast.success('Connecté avec succès !', {
                                    position: "top-right",
                                    autoClose: 3000,
                                    // hideProgressBar: false,
                                    closeOnClick: true,
                                    pauseOnHover: true,
                                    draggable: true,
                                    progress: undefined,
                                });
                                window.location.replace("/admin/dashboard")
                                break;
                            case 500:
                                setoutput("une erreur inconnue vient de se produire !")
                                toast.error('Une erreur serveur est survenue , réessaie encore', {
                                    position: "top-right",
                                    autoClose: 3000,
                                    // hideProgressBar: false,
                                    closeOnClick: true,
                                    pauseOnHover: true,
                                    draggable: true,
                                    progress: undefined,
                                });
                                break;
                            case 405:
                                setoutput("Mot de passe ou adresse email est incorrecte !")
                                toast.error('Mot de passe ou adresse email est incorrecte !', {
                                    position: "top-right",
                                    autoClose: 3000,
                                    // hideProgressBar: false,
                                    closeOnClick: true,
                                    pauseOnHover: true,
                                    draggable: true,
                                    progress: undefined,
                                });
                                break;
                            default:
                                setoutput("une erreur inconnue vient de se produire !")
                                toast.error('Une erreur serveur est survenue , réessaie encore', {
                                    position: "top-right",
                                    autoClose: 3000,
                                    // hideProgressBar: false,
                                    closeOnClick: true,
                                    pauseOnHover: true,
                                    draggable: true,
                                    progress: undefined,
                                });
                                break;
                        }
                    } catch (error) {
                        setoutput("une erreur inconnue vient de se produire !")
                        toast.error('Une erreur serveur est survenue , réessaie encore', {
                            position: "top-right",
                            autoClose: 3000,
                            // hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                        });
                    }
                }else{
                    setisloading(false);
                    setoutput("une erreur inconnue vient de se produire !")
                    toast.error('Une erreur serveur est survenue , réessaie encore', {
                        position: "top-right",
                        autoClose: 3000,
                        // hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                }
            })
        }else{
            if(data.get("password").length < 2){
                toast.warning('Entrer un mot de passe SVP!', {
                    position: "top-right",
                    autoClose: 5000,
                    // hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
            if(!validateEmail(data.get("email"))){
                toast.warning("L'adresse entrer n'est pas valide !", {
                    position: "top-right",
                    autoClose: 5000,
                    // hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }
    };

    React.useEffect(() => {
        // console.log(Cookies.get("AfricaDevCookies"))
        if(window.localStorage.getItem("current-user")) window.location.replace("/admin/dashboard")
    }, [])

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'blue' }}>
            <UserIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Schoola App | Connexion
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Adresse email"
              name="email"
              autoComplete="email"
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Mot de passe"
              type="password"
              id="password"
              autoComplete="current-password"
            />
            <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Rester connecté"
            />
            <Grid style={{}}>
                <Typography variant="body2" component="div" color="error.main" align="center">
                    {output}
                </Typography>
            </Grid>
            <Button
              type="submit"
              disabled={isloading ? true : false}
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2, height: 45 }}
            >
              {isloading
              ?
              <BarWave 
                color="blue" width="30px" height="30px" duration="1s"
              />
              :
              `Connexion`
              }
            </Button>
            <Grid container style={{display: "none"}}>
              <Grid item xs>
                <Link href="#" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
                <Link href="#" variant="body2">
                  {"Don't have an account? Sign Up"}
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 8, mb: 4 }} />
      </Container>
    </ThemeProvider>
  );
}